import { createSlice } from "@reduxjs/toolkit";

const slice = createSlice({
  name: "cart",
  initialState: {
    cartItem: [],
    loading: false,
    total: 0,
  },
  reducers: {
    createCart: (state, actions) => {
      const index = state.cartItem.findIndex(
        (i) => i.id === actions.payload.id
      );
      if (index !== -1) {
        state.cartItem[index] = actions.payload;
      } else {
        state.cartItem.push(actions.payload);
      }
    },
    removeCart: (state, action) => {
      state.cartItem = state.cartItem.filter(
        (i) => i.id !== action.payload._id
      );
    },
    calculatePrice: (state) => {
      const cartItems = state.cartItem;
      for (let i = 0; i < cartItems.length; i++) {
        cartItems[i].subTotal = cartItems[i].quantity * cartItems[i].price;
      }

      state.total = state.cartItem.reduce(
        (value, item) => value + item.subTotal,
        0
      );
    },
    setCartEmpty: (state) => {
      state.cartItem = [];
      state.total = 0;
      state.loading = false;
    },
  },
});

export const { createCart, removeCart, calculatePrice, setCartEmpty } =
  slice.actions;

export default slice.reducer;
