import {
  Box,
  Button,
  Checkbox,
  Divider,
  FormControlLabel,
  FormGroup,
  Grid,
  Stack,
  TextField,
  Typography,
  styled,
} from "@mui/material";
import React, { useState } from "react";
import ProductCard from "../components/subComponent/ProductCard";
import { useGetProductQuery } from "../redux/api/productApi";
import Spinner from "../components/Spinner";

const StyTypo = styled(Typography)`
  font-size: 20px;
  font-weight: 600;
`;
const SearchPage = () => {
  const [name, setName] = useState("");
  const [page, setPage] = useState(1);
  const [sort, setSort] = useState("asc");
  const { isLoading, data } = useGetProductQuery({ name, page, sort });
  const isPrev = page > 1;
  const isLastPage =
    page === Math.ceil(data?.more?.totalProducts / data?.data?.length);

  return (
    data?.data.length > 0 && (
      <Box sx={{ padding: "20px" }}>
        <Grid container spacing={2}>
          <Grid
            item
            xs={1}
            md={1.5}
            sx={{ display: { xs: "none", md: "block" } }}
          >
            <Box sx={{ position: "fixed" }}>
              <Box sx={{ backgroundColor: "", padding: "10px" }}>
                <StyTypo component="p">Category</StyTypo>
                <FormGroup>
                  <FormControlLabel control={<Checkbox />} label="Label" />
                  <FormControlLabel
                    required
                    control={<Checkbox />}
                    label="Required"
                  />
                  <FormControlLabel control={<Checkbox />} label="Disabled" />
                </FormGroup>
              </Box>
              <Box sx={{ backgroundColor: "", padding: "10px" }}>
                <Divider />
                <StyTypo component="p">Colour</StyTypo>
                <FormGroup>
                  <FormControlLabel control={<Checkbox />} label="Label" />
                  <FormControlLabel
                    required
                    control={<Checkbox />}
                    label="Required"
                  />
                  <FormControlLabel control={<Checkbox />} label="Disabled" />
                </FormGroup>
              </Box>
              <Divider />

              <Box sx={{ backgroundColor: "", padding: "10px" }}>
                <StyTypo component="p">Size</StyTypo>
                <FormGroup>
                  <FormControlLabel control={<Checkbox />} label="M" />
                  <FormControlLabel control={<Checkbox />} label="S" />
                  <FormControlLabel control={<Checkbox />} label="XL" />
                </FormGroup>
              </Box>
              <Divider />
            </Box>
          </Grid>
          <Grid item xs={12} md={10.5}>
            <Stack
              direction={"row"}
              gap={"10px"}
              justifyContent={"space-around"}
            >
              <TextField
                id="outlined-basic"
                label="Search Products...."
                variant="outlined"
                size="small"
                sx={{ flexGrow: "1" }}
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
              <select
                name="sort"
                id=""
                defaultValue={"asc"}
                onChange={(e) => {
                  setSort(e.target.value);
                }}
              >
                <option value="asc">Low to High Price</option>
                <option value="dec">Heigh to Low Price</option>
              </select>
            </Stack>
            <Stack
              direction={"row"}
              flexWrap={"wrap"}
              sx={{
                alignItems: "center",
                gap: { xs: "4px", md: "16px" },
                padding: { xs: "3px", sm: "10px" },
                justifyContent: "center",

                marginTop: "10px",
              }}
            >
              {data?.data.length > 0 &&
                (isLoading ? <Spinner /> : <ProductCard data={data?.data} />)}
            </Stack>
            <Box
              sx={{
                textAlign: "right",
                display: "flex",
                justifyContent: "end",
              }}
            >
              <Box>
                <Button
                  onClick={() => {
                    if (page < 1) {
                      setPage(1);
                      return;
                    }
                    setPage((prev) => prev - 1);
                  }}
                  disabled={!isPrev}
                >
                  Prev
                </Button>
                {page} page of{" "}
                {Math.ceil(data?.more?.totalProducts / data?.data?.length)}
                <Button
                  onClick={() => {
                    setPage((prev) => prev + 1);
                  }}
                  disabled={isLastPage}
                >
                  Next
                </Button>
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Box>
    )
  );
};

export default SearchPage;
