import React, { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Box, Button, Stack, Typography, styled } from "@mui/material";
// import FavoriteIcon from "@mui/icons-material/Favorite";
import Spinner from "../components/Spinner";
import ImageViwer from "./subComponent/ImageViwer";
import FeaturedProduct from "./subComponent/FeaturedProduct";
import TCtap from "./subComponent/TCtap";
import SimilarProduct from "./subComponent/SimilarProduct";
import { useGetSingleProductQuery } from "../redux/api/productApi";
import { useDispatch } from "react-redux";
import { createCart } from "../redux/slice/cartReducer";
import toast from "react-hot-toast";
const ProductDetail = () => {
  const navigate = useNavigate();
  const id = useParams().id;
  const dispatch = useDispatch();
  const { isLoading, data } = useGetSingleProductQuery(id);
  const Image = styled("img")({
    borderRadius: "8px",
  });
  const [open, setOpen] = useState(false);
  const [index, setIndex] = useState(0);
  const [imgScr, setImgSrc] = useState(0 || data?.data?.img[index]);
  const [quantity, setQuantity] = useState(1);

  const handleQuantity = (value) => {
    if (value === "inc") {
      setQuantity((prev) => prev + 1);
    } else {
      if (quantity === 1) return;
      setQuantity((prev) => prev - 1);
    }
  };
  const setIndexFuntion = (index) => {
    setImgSrc(data?.data?.img[index]);
  };
  const slideImage = (slide) => {
    if (slide === "right") {
      if (index < data?.data?.img.length - 1) {
        setIndex((prev) => prev + 1);
      }
    } else {
      if (data?.data?.img.length > 0 && index !== 0) {
        setIndex((prev) => prev - 1);
      }
    }
  };
  const handleCheckout = (cart, buy) => {
    dispatch(createCart(cart));
    toast.success("Added To Cart");
    if (buy === "buy") {
      navigate("/checkout");
    }
  };
  return isLoading ? (
    <Spinner />
  ) : (
    <Box>
      <Box
        sx={{
          display: "flex",
          padding: "15px",
          flexDirection: { xs: "column", md: "row" },
          // width: { xs: "100%", sm: "90%" },
          margin: "0 auto",
          height: { md: "80vh" },
        }}
      >
        <Box
          display={"flex"}
          sx={{
            flexDirection: { xs: "column", md: "row-reverse" },
            gap: "10px",
            cursor: "pointer",

            "&::focus": { outline: "none" },
            "&:hover": { background: "none" },
            "&:click": { background: "none" },
            flex: 1,
          }}
        >
          <Image
            src={imgScr || data?.data?.img[index]}
            alt=""
            onClick={() => setOpen(true)}
            style={{ width: { xs: "90%", md: "70%" } }}
          />
          <Box
            sx={{
              display: "flex",

              gap: "6px",
              flexDirection: { xs: "row", md: "column" },
              alignItems: "center",
              flexWrap: "wrap",
              justifyContent: "center",
            }}
          >
            {data?.data?.img.map((i, d) => (
              <Image
                src={i}
                key={d}
                alt={i}
                width={"60px"}
                onClick={() => setIndexFuntion(d)}
              />
            ))}
          </Box>
        </Box>
        <ImageViwer
          open={open}
          setOpen={setOpen}
          slideImage={slideImage}
          img={data?.data?.img[index]}
        />
        <Box
          sx={{
            flex: 1,
            display: "flex",
            flexDirection: "column",
            padding: "20px",
            gap: "10px",
          }}
        >
          <Typography
            variant="h6"
            sx={{ fontSize: { md: "27px" }, fontWeight: { md: 600 } }}
          >
            {data?.data?.title}
          </Typography>
          <Typography
            sx={{ fontSize: { md: "20px" }, fontWeight: { md: 400 } }}
            variant="p"
          >
            {data?.data?.description}
          </Typography>
          <Box
            sx={{ fontSize: { md: "27px" }, color: "tomato", fontWeight: 600 }}
          >
            {data?.data?.price}{" "}
          </Box>
          <Stack
            direction={"row"}
            alignItems={"center"}
            gap={"6px"}
            fontWeight={600}
            sx={{ fontSize: { md: "20px" } }}
          >
            Category: <Typography>{data?.data?.category}</Typography>{" "}
          </Stack>
          <Stack
            direction={"row"}
            alignItems={"center"}
            gap={"6px"}
            fontWeight={600}
            sx={{ fontSize: { md: "20px" } }}
          >
            Stock:
            <Typography
              sx={{ color: data?.data?.stock < 10 ? "red" : "green" }}
            >
              {data?.data?.stock < 10
                ? `Only ${data?.data?.stock} left`
                : "In Stock"}
            </Typography>
          </Stack>
          <Stack
            direction={"row"}
            alignItems={"center"}
            gap={"6px"}
            fontWeight={600}
            sx={{ fontSize: { md: "20px" } }}
          >
            Color:
            <Typography>{`Red`}</Typography>
            <Box
              sx={{
                width: "30px",
                height: "30px",
                borderRadius: "50%",
                backgroundColor: `red`,
              }}
            ></Box>
          </Stack>
          <Stack direction={"row"} alignItems={"center"} gap={1}>
            <Box
              sx={{
                display: "flex",
                border: "1px solid gray",

                width: "25px",
                height: "25px",
                alignItems: "center",
                justifyContent: "center",
                cursor: "pointer",
              }}
              onClick={() => handleQuantity("dec")}
            >
              -
            </Box>
            <Typography
              sx={{
                width: "20px",
                border: "none",
                textAlign: "center",
                cursor: "pointer",
              }}
            >
              {quantity}
            </Typography>
            <Box
              sx={{
                display: "flex",
                border: "1px solid gray",

                width: "25px",
                height: "25px",
                alignItems: "center",
                justifyContent: "center",
                cursor: "pointer",
              }}
              onClick={() => handleQuantity("inc")}
            >
              +
            </Box>
          </Stack>
          <Button
            variant="outlined"
            size="large"
            sx={{ fontWeight: { md: 600 } }}
            onClick={() =>
              handleCheckout({
                id: data?.data?._id,
                title: data?.data?.title,
                price: data?.data?.price,
                stock: data?.data?.stock,
                quantity: 1,
                thumbnail: data?.data?.img[0],
                subTotal: data?.data?.price,
              })
            }
          >
            Add to Cart
          </Button>
          <Button
            variant="contained"
            size="large"
            sx={{ fontWeight: { md: 600 } }}
            onClick={() =>
              handleCheckout(
                {
                  id: data?.data?._id,
                  title: data?.data?.title,
                  price: data?.data?.price,
                  stock: data?.data?.stock,
                  quantity: 1,
                  thumbnail: data?.data?.img[0],
                  subTotal: data?.data?.price,
                },
                "buy"
              )
            }
          >
            Buy Now
          </Button>
        </Box>
      </Box>
      <Box>
        <TCtap desc={data?.data?.description} />
      </Box>
      <Box>
        <SimilarProduct />
      </Box>
      <Box>
        <FeaturedProduct />
      </Box>
    </Box>
  );
};

export default ProductDetail;
