import React from "react";
import { Box, Button, Drawer, Stack, Typography } from "@mui/material";
import CartItem from "./CartItem";
import CloseIcon from "@mui/icons-material/Close";
const Wishlist = ({ openWishlist, setOpenWishlist }) => {
  return (
    <Drawer
      anchor={"right"}
      open={openWishlist}
      onClose={() => setOpenWishlist(false)}
    >
      <Stack
        sx={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-between",
          height: "100%",
          padding: "1px",
          minWidth: "300px",
          maxWidth: "300px",
          position: "relative",
        }}
      >
        <Button
          variant="contained"
          size="small"
          sx={{
            position: "absolute",
            top: "8px",
            left: "8px",
            cursor: "pointer",
          }}
          onClick={() => setOpenWishlist(false)}
        >
          <CloseIcon />
        </Button>
        <Box>
          <Typography fontSize={26} textAlign={"center"}>
            Wishlist
          </Typography>
        </Box>
        <Stack
          flexGrow={1}
          padding={"10px"}
          direction={"column"}
          gap={"10px"}
          sx={{
            overflowY: "scroll",
            "&::-webkit-scrollbar": {
              display: "none",
            },
          }}
        >
          <CartItem />
          <CartItem />
          <CartItem />
          <CartItem />
          <CartItem />
          <CartItem />
          <CartItem />
          <CartItem />
        </Stack>
        <Stack gap={"10px"}>
          <Stack direction={"row"} justifyContent={"space-between"}>
            <Typography fontSize={22}>Total</Typography>
            <Typography fontSize={22} color={"tomato"}>
              5000
            </Typography>
          </Stack>
          <Button variant="contained" fullWidth>
            Checkout
          </Button>
        </Stack>
      </Stack>
    </Drawer>
  );
};

export default Wishlist;
