import React from "react";
import { Box } from "@mui/material";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import KeyboardArrowLeftIcon from "@mui/icons-material/KeyboardArrowLeft";
import { Modal } from "@mui/material";
import "./imgViwer.css";
import { useRef } from "react";
import CloseIcon from "@mui/icons-material/Close";
const ImageViwer = ({ open, setOpen, slideImage, img }) => {
  const ref = useRef();

  return (
    <Modal
      open={open}
      onClose={() => setOpen(false)}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
      sx={{
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        padding: "30px",
      }}
    >
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",

          position: "relative",
          width: { sm: "80vw" },
          height: { sm: "90vh" },
          "&::focus": { outline: "none" },
        }}
      >
        <CloseIcon
          sx={{
            position: "absolute",
            top: 0,
            right: 0,
            color: "white",
            fontSize: "35px",
            fontWeight: 700,
            cursor: "pointer",
          }}
          onClick={() => {
            setOpen(false);
          }}
        />
        <img
          ref={ref}
          src={img}
          className="imageViewer"
          alt=""
          style={{
            height: "100%",
            width: "100%",
          }}
        />

        <KeyboardArrowRightIcon
          onClick={() => {
            slideImage("right");
          }}
          sx={{
            position: "absolute",
            right: "20px",
            color: "white",
            fontSize: "45px",
            fontWeight: 700,
            cursor: "pointer",
          }}
        />

        <KeyboardArrowLeftIcon
          onClick={() => {
            slideImage("left");
          }}
          sx={{
            position: "absolute",
            left: "20px",
            color: "white",
            fontSize: "45px",
            fontWeight: 700,
            cursor: "pointer",
          }}
        />
      </Box>
    </Modal>
  );
};

export default ImageViwer;
