import React from "react";
import { Box, Button, Stack, Typography } from "@mui/material";
import StepperComponent from "../../components/subComponent/Stepper";
import { useAllOrdersQuery } from "../../redux/api/orderApi";
import { useSelector } from "react-redux";
import Spinner from "../../components/Spinner";
import { useNavigate } from "react-router-dom";
import toast from "react-hot-toast";
import axios from "axios";
import moment from "moment";
const Orders = () => {
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.users);

  const { data } = useAllOrdersQuery(user._id, {}, { caches: 1000 });
  const updateStatusHandler = async (id) => {
    try {
      const { data } = await axios.put(
        `${process.env.REACT_APP_BEURL}/api/order/${id}`
      );
      if (data.success) {
        toast.success(data.message);
      }
    } catch (error) {
      toast.error("Error Occured");
    }
  };
  return data?.orders.length > 0 ? (
    data?.orders.map((i, x) => (
      <Stack
        key={x}
        sx={{
          border: "1px solid #cccccc",
          padding: "10px",
          borderRadius: "5px",
        }}
      >
        <Typography sx={{ fontSize: "15px", color: "#808080" }}>
          Order On: {moment(i.createdAt).format("DD/MM/YY h:mm:ss a")}
        </Typography>
        <Typography sx={{ fontSize: "15px", color: "#808080" }}>
          Estimated Delivery: 11/12/2003
        </Typography>
        <Stack
          sx={{
            display: "flex",
            flexDirection: { xs: "column", sm: "row" },
            justifyContent: "space-between",
          }}
        >
          <Box
            sx={{
              display: "flex",

              paddingY: "10px",
              position: "relative",
              flexDirection: "column",
            }}
          >
            <Box
              sx={{
                display: "flex",

                paddingY: "10px",
                flexWrap: "wrap",
              }}
            >
              {i.orders.map((i, x) => (
                <Box>
                  <img
                    key={x}
                    src={i.thumbnail}
                    alt=""
                    style={{
                      width: "50px",
                      height: "50px",
                      borderRadius: "4px",
                      padding: "6px",
                      margin: "0 auto",
                    }}
                    onClick={() => navigate(`/product/${i.id}`)}
                  />
                </Box>
              ))}
            </Box>

            <Stack
              direction={"column"}
              gap={"10px"}
              paddingX={"10px"}
              flexWrap={"wrap"}
              borderRadius={"4px"}
            >
              <Stack direction={"row"} alignItems={"center"} gap={1}>
                <Typography>QTY:{i.orders.length}</Typography>

                <Typography>Total:{i.total}</Typography>
              </Stack>
            </Stack>
          </Box>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",

              gap: "10px",
            }}
          >
            <StepperComponent
              step={
                i.status === "processing" ? 0 : i.status === "shipped" ? 1 : 3
              }
            />
            <Button
              variant="outlined"
              onClick={() => navigate(`/my-orders/${i._id}`)}
            >
              Viwe
            </Button>
            <Button
              variant="contained"
              onClick={() => updateStatusHandler(i._id)}
            >
              Update
            </Button>
          </Box>
        </Stack>
      </Stack>
    ))
  ) : (
    <Spinner />
  );
};

export default Orders;
