import { Box, ThemeProvider, createTheme } from "@mui/material";

import NavBar from "./components/NavBar";

import { useState, useEffect, useRef } from "react";
import BottomNavigationBar from "./components/BottomNavigation";
import Footer from "./components/Footer";
import { Routes, Route, useLocation } from "react-router-dom";
import Home from "./pages/Home";
import ProductDetail from "./components/ProductDetail";
import SearchPage from "./pages/SearchPage";
import About from "./pages/About";
import Contact from "./pages/Contact";
import PageNotFound from "./components/PageNotFound";
import MyOrders from "./pages/MyOrders";
import Checkout from "./pages/Checkout";
import Login from "./pages/Login";
import Register from "./pages/Register";
import { useDispatch } from "react-redux";
import { userExist } from "./redux/slice/userReducer";
import { Toaster } from "react-hot-toast";
import axios from "axios";
import CreateProduct from "./components/Admin/CreateProduct";
import Dashboard from "./components/Admin/Dashboard";
import AllProduct from "./components/Admin/AllProduct";
import OrderDetail from "./pages/OrderDetail";
import Orders from "./components/Admin/Orders";
import UpdateOrder from "./components/Admin/UpdateOrder";
import AdminDashboard from "./components/Admin/AdminDashboard";
function App() {
  const [mode, setMode] = useState("light");
  const [openCart, setOpenCart] = useState();

  const location = useLocation();
  const dispatch = useDispatch();
  // const { isLoading, data, error } = useGetUserQuery(
  //   "65e71998d6e97deccdf6680a"
  // );
  const setUserInToken = async () => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_BEURL}/api/user/get-user`,
        {
          headers: {
            token: localStorage.getItem("token"),
          },
        }
      );

      dispatch(userExist(data.user));
    } catch (error) {
      console.log(error.message);
    }
  };
  useEffect(() => {
    setUserInToken();
  }, []);

  const prevLocationRef = useRef(location);
  const themes = createTheme({
    palette: {
      mode: mode,
    },
  });
  useEffect(() => {}, []);

  useEffect(() => {
    if (prevLocationRef.current.pathname !== location.pathname) {
      window.scrollTo(0, 0);
      prevLocationRef.current = location;
    }
  }, [location]);
  return (
    <ThemeProvider theme={themes}>
      <Box
        sx={{
          bgcolor: "background.default",
          color: "text.primary",
        }}
      >
        <NavBar
          setMode={setMode}
          mode={mode}
          setOpenCart={setOpenCart}
          openCart={openCart}
        />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/home" element={<Home />} />
          <Route path="/products" element={<SearchPage />} />
          <Route path="/product/:id" element={<ProductDetail />} />
          <Route path="/search" element={<SearchPage />} />
          <Route path="/about" element={<About />} />
          <Route path="/contact" element={<Contact />} />
          <Route path="/my-orders" element={<MyOrders />} />
          <Route path="/my-orders/:id" element={<OrderDetail />} />
          <Route path="/checkout" element={<Checkout />} />
          <Route path="/login" element={<Login />} />
          <Route path="/register" element={<Register />} />
          <Route path="/*" element={<PageNotFound />} />

          {/* {Admin Routes} */}
          <Route
            path="/admin/dashboard"
            element={
              <Dashboard>
                <AdminDashboard />
              </Dashboard>
            }
          />

          <Route
            path="/admin/new-product"
            element={
              <Dashboard>
                <CreateProduct />
              </Dashboard>
            }
          />
          <Route
            path="/admin/product"
            element={
              <Dashboard>
                <AllProduct />
              </Dashboard>
            }
          />
          <Route
            path="/admin/orders"
            element={
              <Dashboard>
                <Orders />
              </Dashboard>
            }
          />
          <Route
            path="/order/update/:id"
            element={
              <Dashboard>
                <UpdateOrder />
              </Dashboard>
            }
          />
        </Routes>
        <BottomNavigationBar setOpenCart={setOpenCart} />
        <Footer />
        <Toaster position="top-center" />
      </Box>
    </ThemeProvider>
  );
}

export default App;
