import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { createCart } from "../../redux/slice/cartReducer";
import toast from "react-hot-toast";
const ProductCard = ({ data }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const handleCheckout = (cart) => {
    dispatch(createCart(cart));
    toast.success("Added To Cart");
  };
  return data.map((i, index) => (
    <Card
      sx={{
        minWidth: { xs: 140, md: 200 },
        maxWidth: { xs: 140, md: 210 },
        height: "100%",
        paddingX: "5px",
      }}
      key={index}
    >
      <CardMedia
        onClick={() => navigate(`/product/${i._id}`)}
        component="img"
        alt="green iguana"
        sx={{ cursor: "pointer" }}
        image={i.img[0]}
        height={"200px"}
      />
      <CardContent sx={{ cursor: "pointer" }}>
        <Typography
          onClick={() => navigate("/product/dfdfdf")}
          gutterBottom
          component="p"
          sx={{
            fontSize: { xs: "14px", sm: "16px" },
            fontWeight: 600,
            whiteSpace: "nowrap",
            overflow: "hidden",
            textOverflow: "ellipsis",
          }}
        >
          {i.title}
        </Typography>
        <Typography
          sx={{
            whiteSpace: "nowrap",
            overflow: "hidden",
            textOverflow: "ellipsis",
          }}
          variant="body2"
          color="text.secondary"
        >
          {i.description}
        </Typography>
        <Typography sx={{ color: "tomato", fontWeight: 600 }}>
          {" "}
          {i.price}
        </Typography>
      </CardContent>
      <CardActions sx={{ flexDirection: { xs: "column" }, gap: "10px" }}>
        <Button
          variant="contained"
          size="small"
          sx={{ width: "100%" }}
          onClick={() =>
            handleCheckout({
              id: i._id,
              title: i.title,
              price: i.price,
              stock: i.stock,
              quantity: 1,
              thumbnail: i.img[0],
              subTotal: i.price,
            })
          }
        >
          Add To Cart
        </Button>
      </CardActions>
    </Card>
  ));
};

export default ProductCard;
