import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  Container,
  Divider,
  FormControl,
  FormControlLabel,
  Grid,
  Modal,
  Radio,
  RadioGroup,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import CartItem from "../components/subComponent/CartItem";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import toast from "react-hot-toast";

import { setCartEmpty } from "../redux/slice/cartReducer";
const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 250,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

const Checkout = () => {
  const { user } = useSelector((state) => state.users);

  const [open, setOpen] = React.useState(false);
  const [address, setAddress] = useState(user?.address);
  const [landMark, setLandMark] = useState(user?.landMark);
  const [city, setCity] = useState(user?.city);
  const [state, setState] = useState(user?.state);
  const [pin, setPin] = useState(user?.pin);
  const [number, setNumber] = useState(user?.number);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const { cartItem, total } = useSelector((state) => state.cart);

  useEffect(() => {
    if (cartItem.length === 0) {
      navigate("/");
    }
    // eslint-disable-next-line
  }, [cartItem]);
  const handleCheckout = async () => {
    try {
      const {
        data: { key },
      } = await axios.get(`${process.env.REACT_APP_BEURL}/api/payment/key`);

      const {
        data: { data },
      } = await axios.post(
        `${process.env.REACT_APP_BEURL}/api/payment/instance`,
        { amount: total }
      );

      var options = {
        key, // Enter the Key ID generated from the Dashboard
        amount: data.amount, // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
        currency: "INR",
        name: "Hemanta Kumar Paswan",
        description: "Test Transaction",

        order_id: data.order_id, //This is a sample Order ID. Pass the `id` obtained in the response of Step 1
        handler: async function (response) {
          try {
            const { data } = await axios.post(
              `${process.env.REACT_APP_BEURL}/api/order/new`,
              {
                orders: cartItem,
                total,
                shipping: `${
                  (address,
                  " ",
                  landMark,
                  " ",
                  city,
                  " ",
                  state,
                  " ",
                  pin,
                  " ",
                  number)
                }`,
                userId: user._id,
              }
            );
            await axios.post(
              `${process.env.REACT_APP_BEURL}/api/payment/verify`,
              response
            );
            if (data.success) {
              navigate("/my-orders");
              toast.success(data.message);
              setTimeout(() => {
                dispatch(setCartEmpty());
              }, 1000);
            }
          } catch (error) {
            console.log(error);
          }
        },
        prefill: {
          name: user.name,
          email: user.email,
          contact: user.number,
        },
        notes: {
          address: "Razorpay Corporate Office",
        },
        theme: {
          color: "#15388b",
        },
      };
      var rzp1 = new window.Razorpay(options);
      rzp1.on("payment.failed", function (response) {
        alert(response.error.code);
        alert(response.error.description);
        alert(response.error.source);
        alert(response.error.step);
        alert(response.error.reason);
        alert(response.error.metadata.order_id);
        alert(response.error.metadata.payment_id);
      });

      rzp1.open();
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Box sx={{ padding: "15px" }}>
      <Grid container spacing={2}>
        <Grid
          item
          xs={12}
          md={8}
          flex={1}
          sx={{ display: { xs: "none", md: "flex" } }}
        >
          <Container
            sx={{
              paddingY: "10px",
              gap: "10px",
              display: "flex",
              flexDirection: "column",
              padding: "10px",
              border: "1px solid #808080",
            }}
          >
            <Typography>Order Item</Typography>
            <Box
              sx={{
                maxHeight: "75vh",

                overflowX: "scroll",
                display: "flex",
                flexDirection: "column",
                gap: "10px",
              }}
            >
              <CartItem />
            </Box>
          </Container>
        </Grid>
        <Grid item xs={12} md={4} flex={1}>
          <Container
            sx={{
              border: "1px solid #808080",
              padding: "15px",
              height: "100%",
            }}
          >
            <Stack direction={"row"} gap={"10px"}>
              <Typography fontSize={"20px"}>Shipping Address</Typography>
              <Button
                variant="outlined"
                size="small"
                sx={{ marginLeft: 0 }}
                onClick={handleOpen}
              >
                Edit
              </Button>
              <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
              >
                <Box sx={style}>
                  <TextField
                    id="standard-required"
                    label="Address"
                    variant="standard"
                    value={address}
                    onChange={(e) => setAddress(e.target.value)}
                  />
                  <TextField
                    id="standard-search"
                    label="Land Mark"
                    variant="standard"
                    onChange={(e) => setLandMark(e.target.value)}
                    value={landMark}
                  />
                  <TextField
                    id="standard-disabled"
                    label="City"
                    variant="standard"
                    onChange={(e) => setCity(e.target.value)}
                    value={city}
                  />
                  <TextField
                    id="standard-password-input"
                    label="State"
                    variant="standard"
                    onChange={(e) => setState(e.target.value)}
                    value={state}
                  />
                  <TextField
                    id="standard-read-only-input"
                    label="Phone Number"
                    variant="standard"
                    type="number"
                    onChange={(e) => setNumber(e.target.value)}
                    value={number}
                  />
                  <TextField
                    id="standard-number"
                    label="Pincode"
                    type="number"
                    variant="standard"
                    onChange={(e) => setPin(e.target.value)}
                    value={pin}
                  />

                  <Box sx={{ textAlign: "right", marginTop: "10px" }}>
                    <Button variant="outlined" onClick={() => setOpen(false)}>
                      Done
                    </Button>
                  </Box>
                </Box>
              </Modal>
            </Stack>
            <Divider sx={{ marginY: "5px" }} />

            {pin && number && address ? (
              <Container>
                <Typography>{address}</Typography>
                <Typography>{landMark}</Typography>
                <Typography>
                  {city} {state}
                </Typography>
                <Typography>{pin}</Typography>
                <Typography>{number}</Typography>
              </Container>
            ) : (
              <Button
                variant="contained"
                size="small"
                fullWidth
                onClick={() => setOpen(true)}
              >
                Add Address
              </Button>
            )}

            <Typography fontSize={"20px"}>Select Payment Mode</Typography>
            <Divider />

            <Container>
              <FormControl>
                <RadioGroup
                  aria-labelledby="demo-radio-buttons-group-label"
                  defaultValue="upi"
                  name="radio-buttons-group"
                >
                  <FormControlLabel
                    value="upi"
                    control={<Radio />}
                    label="UPI"
                  />
                  <FormControlLabel
                    value="card"
                    control={<Radio />}
                    label="Card"
                  />
                  <FormControlLabel
                    value="cod"
                    control={<Radio />}
                    label="COD"
                  />
                </RadioGroup>
              </FormControl>
            </Container>
            <Box
              sx={{
                display: "flex",
                justifyContent: "space-between",
                padding: "6px",
              }}
            >
              <Typography fontSize={"25px"} fontWeight={600}>
                Total
              </Typography>
              <Typography fontSize={"25px"} fontWeight={600} color={"tomato"}>
                {total}
              </Typography>
            </Box>
            <Button variant="contained" fullWidth onClick={handleCheckout}>
              Pay Now
            </Button>
          </Container>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Checkout;
