import { Box, Stack, Typography } from "@mui/material";
import React from "react";
import DeleteIcon from "@mui/icons-material/Delete";
import { useDispatch, useSelector } from "react-redux";
import { createCart, removeCart } from "../../redux/slice/cartReducer";

const CartItem = () => {
  const dispatch = useDispatch();
  const { cartItem } = useSelector((state) => state.cart);

  const increment = (item) => {
    if (item.quantity >= item.stock) return;
    dispatch(
      createCart({
        ...item,
        quantity: item.quantity + 1,
        // subTotal: (item.quantity + 1) * item.price,
      })
    );
  };
  const decrement = (item) => {
    if (item.quantity === 1) return;
    dispatch(
      createCart({
        ...item,
        quantity: item.quantity - 1,
        // subTotal: item.quantity * item.price,
      })
    );
  };

  return (
    <>
      {cartItem.length > 0 ? (
        cartItem.map((i, index) => {
          return (
            <Box
              key={index}
              sx={{
                display: "flex",

                border: "1px solid gray",
                paddingY: "10px",
                position: "relative",
                borderRadius: "5px",
                maxWidth: "100%",
              }}
            >
              <Box>
                <img
                  src={i.thumbnail}
                  alt=""
                  style={{
                    width: "50px",
                    height: "50px",
                    borderRadius: "4px",
                    padding: "6px",
                    margin: "0 auto",
                  }}
                />
              </Box>

              <Stack
                direction={"column"}
                gap={"10px"}
                paddingX={"10px"}
                flexWrap={"wrap"}
                borderRadius={"4px"}
              >
                <Typography
                  component={"span"}
                  sx={{
                    whiteSpace: "nowrap",
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    width: "150px",
                  }}
                >
                  {i.title}
                </Typography>
                <Stack direction={"row"} alignItems={"center"} gap={1}>
                  <Box
                    sx={{
                      display: "flex",
                      border: "1px solid gray",

                      width: "25px",
                      height: "25px",
                      alignItems: "center",
                      justifyContent: "center",
                      cursor: "pointer",
                    }}
                    onClick={() => decrement(i)}
                  >
                    -
                  </Box>
                  <input
                    type="text"
                    value={i.quantity}
                    defaultValue={i.quantity}
                    style={{
                      width: "20px",
                      border: "none",
                      textAlign: "center",
                      cursor: "pointer",
                    }}
                  />
                  <Box
                    sx={{
                      display: "flex",
                      border: "1px solid gray",

                      width: "25px",
                      height: "25px",
                      alignItems: "center",
                      justifyContent: "center",
                      cursor: "pointer",
                    }}
                    onClick={() => increment(i)}
                  >
                    +
                  </Box>
                </Stack>
              </Stack>
              <Typography>{i.price * i.quantity}</Typography>
              <Box sx={{ position: "absolute", bottom: 0, right: "6px" }}>
                <DeleteIcon
                  onClick={() => dispatch(removeCart({ _id: i.id }))}
                />
              </Box>
            </Box>
          );
        })
      ) : (
        <Box>
          <Typography>No products in cart</Typography>
        </Box>
      )}
    </>
  );
};

export default CartItem;
