import React, { useState } from "react";
import {
  Box,
  List,
  ListItem,
  ListItemText,
  Tab,
  Tabs,
  Typography,
} from "@mui/material";
import Reviwe from "./Reviwe";

const TCtap = ({ desc }) => {
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <Box>
      <Tabs value={value} onChange={handleChange} centered>
        <Tab label="Description" />
        <Tab label="Return Policy" />
        <Tab label="Reviwe" />
      </Tabs>
      {value === 0 && (
        <Box sx={{ padding: "20px", textAlign: "center" }}>
          {desc ||
            " Lorem ipsum dolor sit amet consectetur adipisicing elit. Laborum a architecto ipsa aliquam quibusdam perspiciatis cupiditate tenetur omnis numquam nesciunt?"}
        </Box>
      )}
      {value === 1 && (
        <Box
          sx={{
            display: "flex",
            padding: "20px",
            flexWrap: "wrap",
            gap: "5px",
            justifyContent: "center",
          }}
        >
          <Box>
            <Typography variant="h6">
              Terms and Conditions for Returns and Unboxing Policy
            </Typography>
            <Typography variant="body1">
              These Terms and Conditions ("Terms") govern your use of the [Your
              Ecommerce Website Name] website and its associated services
              (collectively, the "Service").
            </Typography>

            <Box sx={{ mt: 3 }}>
              <Typography variant="h6">Unboxing Policy:</Typography>
              <Typography variant="body1">
                To ensure product authenticity and condition, we require
                customers to record a 360-degree video while unboxing their
                purchased product(s). This video serves as evidence in case of
                any return or exchange requests.
              </Typography>
            </Box>

            <Box sx={{ mt: 3 }}>
              <Typography variant="h6">
                Return and Exchange Eligibility:
              </Typography>
              <List>
                <ListItem>
                  <ListItemText
                    primary="Wrong Item Received"
                    secondary="If you receive an item different from your order, please record a 360-degree unboxing video showcasing the discrepancy."
                  />
                </ListItem>
                <ListItem>
                  <ListItemText
                    primary="Damaged Item"
                    secondary="If your item arrives damaged, please record a 360-degree unboxing video highlighting the damage."
                  />
                </ListItem>
              </List>
            </Box>

            <Box sx={{ mt: 3 }}>
              <Typography variant="h6">Unboxing Video Requirements:</Typography>
              <List>
                <ListItem>
                  <ListItemText
                    primary="Complete and uninterrupted 360-degree recording"
                    secondary="The video must be a complete and uninterrupted 360-degree recording of the entire unboxing process, starting from opening the package to revealing the product."
                  />
                </ListItem>
                <ListItem>
                  <ListItemText
                    primary="Clear and well-lit"
                    secondary="The video must be clear and well-lit, allowing us to properly assess the product condition and any potential issues."
                  />
                </ListItem>
                <ListItem>
                  <ListItemText
                    primary="Uploaded within 48 hours"
                    secondary="The video must be uploaded within 48 hours of receiving the product."
                  />
                </ListItem>
              </List>
            </Box>

            <Box sx={{ mt: 3 }}>
              <Typography variant="h6">Return and Exchange Process:</Typography>
              <Typography variant="body1">
                1. Contact our customer support team within 48 hours of
                receiving the product to initiate the return or exchange
                process. <br />
                2. Provide your order number and a brief description of the
                issue. <br />
                3. Upload your 360-degree unboxing video. <br />
                4. Once we receive and review your request, we will contact you
                with further instructions.
              </Typography>
            </Box>

            <Box sx={{ mt: 3 }}>
              <Typography variant="h6">Exceptions:</Typography>
              <Typography variant="body1">
                We reserve the right to refuse returns or exchanges at our sole
                discretion for the following reasons:
              </Typography>
              <List>
                <ListItem>
                  <ListItemText primary="Items not returned in original packaging and condition" />
                </ListItem>
                <ListItem>
                  <ListItemText primary="Items damaged after receipt by the customer" />
                </ListItem>
                <ListItem>
                  <ListItemText primary="Items returned without a valid reason or exceeding the 48-hour timeframe" />
                </ListItem>
                <ListItem>
                  <ListItemText primary="Any other violation of these Terms" />
                </ListItem>
              </List>
            </Box>

            <Box sx={{ mt: 3 }}>
              <Typography variant="h6">Governing Law:</Typography>
              <Typography variant="body1">
                These Terms shall be governed by and construed in accordance
                with the laws of [Your Jurisdiction].
              </Typography>
            </Box>
          </Box>
        </Box>
      )}
      {value === 2 && (
        <Box
          sx={{
            display: "flex",
            padding: "20px",
            flexWrap: "wrap",
            gap: "5px",
            justifyContent: "center",

            maxHeight: "80vh",
            overflowX: "scroll",
            "&::-webkit-scrollBar": {
              display: "none",
            },
          }}
        >
          <Reviwe />
          <Reviwe />
          <Reviwe />
          <Reviwe />
        </Box>
      )}
    </Box>
  );
};

export default TCtap;
