import React, { useRef } from "react";

import ProductCard from "../../components/subComponent/ProductCard";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import KeyboardArrowLeftIcon from "@mui/icons-material/KeyboardArrowLeft";
import { Box, Divider, Typography } from "@mui/material";
import { useGetProductQuery } from "../../redux/api/productApi";

const FeaturedProduct = () => {
  const containerRef = useRef(null);
  const { data } = useGetProductQuery();

  const navigation = (dir) => {
    const container = containerRef.current;
    let scrollAmount;
    if (dir === "left") {
      scrollAmount = container?.scrollLeft - (container.offsetWidth + 20);
    } else {
      scrollAmount = container?.scrollLeft + (container.offsetWidth + 20);
    }

    container.scrollTo({
      left: scrollAmount,
      behavior: "smooth",
    });
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        gap: "10px",
        margin: "30px 0",

        position: "relative",
      }}
    >
      <Typography
        sx={{
          fontSize: { xs: "20px", sm: "22px", md: "28px" },
          fontWeight: 600,
          paddingX: "10px",
        }}
        variant="h4"
      >
        Featured Products
      </Typography>
      <Divider />
      <Box
        ref={containerRef}
        sx={{
          display: "flex",
          gap: "10px",
          overflowX: "scroll",

          position: "relative",
          "&::-webkit-scrollbar": {
            display: "none",
          },
        }}
      >
        {data?.data?.length > 0 && <ProductCard data={data?.data} />}
      </Box>

      <KeyboardArrowLeftIcon
        onClick={() => {
          navigation("left");
        }}
        sx={{
          position: "absolute",
          top: "50%",
          left: "20px",
          color: "black",
          fontSize: "45px",
          fontWeight: 700,
          cursor: "pointer",
          border: "1px solid gray",
          borderRadius: "50%",
          backgroundColor: "white",
          display: { xs: "none", sm: "flex" },
        }}
      />

      <KeyboardArrowRightIcon
        onClick={() => {
          navigation("right");
        }}
        sx={{
          position: "absolute",
          top: "50%",
          right: "20px",
          color: "blsck",
          fontSize: "45px",
          fontWeight: 700,
          cursor: "pointer",
          border: "1px solid gray",
          borderRadius: "50%",
          backgroundColor: "white",
          display: { xs: "none", sm: "flex" },
        }}
      />
    </Box>
  );
};

export default FeaturedProduct;
