import { Container, Typography } from "@mui/material";
import React from "react";
// import Stepper from "../components/subComponent/Stepper";
import OrderItems from "../components/subComponent/OrderItems";
const MyOrders = () => {
  return (
    <Container
      sx={{
        paddingY: "10px",
        gap: "15px",
        display: "flex",
        flexDirection: "column",
      }}
    >
      <Typography fontSize={"30px"}>My Orders</Typography>
      <OrderItems />
    </Container>
  );
};

export default MyOrders;
