import { Box, Button, Drawer, Stack, Typography } from "@mui/material";
import React, { useEffect } from "react";
import CartItem from "./CartItem";
import CloseIcon from "@mui/icons-material/Close";
import { useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { calculatePrice } from "../../redux/slice/cartReducer";

const Cart = ({ openCart, setOpenCart }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { cartItem, total } = useSelector((state) => state.cart);

  useEffect(() => {
    dispatch(calculatePrice());
    // eslint-disable-next-line
  }, [cartItem]);
  return (
    <Drawer anchor={"right"} open={openCart} onClose={() => setOpenCart(false)}>
      <Stack
        sx={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-between",
          height: "100%",
          padding: "1px",
          minWidth: "300px",
          maxWidth: "300px",
          position: "relative",
        }}
      >
        <Button
          variant="contained"
          size="small"
          sx={{
            position: "absolute",
            top: "8px",
            left: "8px",
            cursor: "pointer",
          }}
          onClick={() => setOpenCart(false)}
        >
          <CloseIcon />
        </Button>
        <Box>
          <Typography fontSize={26} textAlign={"center"}>
            Cart
          </Typography>
        </Box>
        <Stack
          flexGrow={1}
          padding={"10px"}
          direction={"column"}
          gap={"10px"}
          sx={{
            overflowY: "scroll",
            "&::-webkit-scrollbar": {
              display: "none",
            },
          }}
        >
          <CartItem />
        </Stack>
        <Stack gap={"10px"} paddingY={1.5} paddingX={1}>
          <Stack direction={"row"} justifyContent={"space-between"}>
            <Typography fontSize={22}>Total</Typography>
            <Typography fontSize={22} color={"tomato"}>
              {total}
            </Typography>
          </Stack>
          <Button
            variant="contained"
            fullWidth
            onClick={() => {
              navigate("/checkout");
              setOpenCart(false);
            }}
          >
            Checkout
          </Button>
        </Stack>
      </Stack>
    </Drawer>
  );
};

export default Cart;
