import { Box, Button, Stack, Typography } from "@mui/material";
import React from "react";
import ShoppingBagIcon from "@mui/icons-material/ShoppingBag";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import MonetizationOnIcon from "@mui/icons-material/MonetizationOn";
import "./AdminCss.css";
import {
  flexRender,
  useReactTable,
  getCoreRowModel,
  getPaginationRowModel,
} from "@tanstack/react-table";
import { useAllOrdersQuery } from "../../redux/api/orderApi";

const styledSvg = (color, box) => {
  const svgStyle = {
    fontSize: "40px",
    color: color,
    boxShadow: `3px 5px 8px ${box} `,
    borderRadius: "50%",
    padding: "5px",
  };
  return svgStyle;
};
const styledDiv = {
  bgcolor: "white",
  padding: "20px",
  display: "flex",
  alignItems: "start",
  gap: "16px",
  flexGrow: 1,
};

const AdminDashboard = () => {
  const { data: res } = useAllOrdersQuery();

  const columns = [
    {
      header: "ID",
      accessorKey: "_id",
    },
    {
      header: "Total",
      accessorKey: "total",
    },
    {
      header: "Shipping",
      accessorKey: "shipping",
    },
    {
      header: "Status",
      accessorKey: "status",
      cell: (ce) => (
        <div
          variant="contained"
          style={{
            color:
              ce.getValue() === "processing"
                ? "red"
                : ce.getValue() === "shipped"
                ? "yellow"
                : "purple",
          }}
        >
          {ce.getValue()}
        </div>
      ),
    },
    {
      header: "Update",

      cell: (ce) => (
        <Button
          variant="contained"
          onClick={() => console.log("first", ce.getValue())}
        >
          Update
        </Button>
      ),
    },
  ];
  const data = res?.orders;
  const table = useReactTable({
    data,
    columns,
    getCoreRowModel: getCoreRowModel(),
    getPaginationRowModel: getPaginationRowModel(),
  });
  return (
    <Box sx={{ padding: "20px", background: "#eeeeee" }}>
      <Typography variant="h5" fontWeight={"600"} marginBottom={"20px"}>
        Dashboard
      </Typography>
      <Stack display={"flex"} direction={"row"} gap={"20px"} borderRadius={6}>
        <Box sx={styledDiv}>
          <MonetizationOnIcon sx={styledSvg("red", "red")} />
          <Box>
            <Typography sx={{ fontWeight: 600, color: "#bbbbbb" }}>
              Total Sales
            </Typography>
            <Typography fontWeight={"600"} fontSize={"18px"}>
              68789978
            </Typography>
          </Box>
        </Box>
        <Box sx={styledDiv}>
          <ShoppingCartIcon sx={styledSvg("green", "green")} color="red" />
          <Box>
            <Typography sx={{ fontWeight: 600, color: "#bbbbbb" }}>
              Total Orders
            </Typography>
            <Typography fontWeight={"600"} fontSize={"18px"}>
              687
            </Typography>
          </Box>
        </Box>
        <Box sx={styledDiv}>
          <ShoppingBagIcon sx={styledSvg("blue", "blue")} color="red" />
          <Box>
            <Typography sx={{ fontWeight: 600, color: "#bbbbbb" }}>
              Total Products
            </Typography>
            <Typography fontWeight={"600"} fontSize={"18px"}>
              6878
            </Typography>
          </Box>
        </Box>
      </Stack>
      <Stack paddingY={"20px"}>
        <Typography
          variant="h6"
          fontWeight={"600"}
          marginBottom={"16px"}
          color={"#383737ee"}
        >
          Latest Orders
          {res?.orders?.length > 0 && (
            <table id="customers">
              {table.getHeaderGroups().map((hearderGroups) => (
                <tr key={hearderGroups.id}>
                  {hearderGroups.headers.map((header) => (
                    <th key={header.id}>
                      {flexRender(
                        header.column.columnDef.header,
                        header.getContext()
                      )}
                    </th>
                  ))}
                </tr>
              ))}

              <tbody>
                {table.getRowModel().rows.map((rowmodle) => (
                  <tr key={rowmodle.id}>
                    {rowmodle.getVisibleCells().map((cell) => (
                      <td key={cell.id}>
                        {flexRender(
                          cell.column.columnDef.cell,
                          cell.getContext()
                        )}
                      </td>
                    ))}
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </Typography>
        <Stack></Stack>
      </Stack>
    </Box>
  );
};

export default AdminDashboard;
