import {
  AppBar,
  Avatar,
  Badge,
  Box,
  Drawer,
  IconButton,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Menu,
  MenuItem,
  Toolbar,
  Typography,
  styled,
  Switch,
} from "@mui/material";
import React, { useState } from "react";
import MenuIcon from "@mui/icons-material/Menu";
import MaleIcon from "@mui/icons-material/Male";
import SearchIcon from "@mui/icons-material/Search";
import FavoriteIcon from "@mui/icons-material/Favorite";
import List from "@mui/material/List";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import { Link, useNavigate } from "react-router-dom";
import Cart from "./subComponent/Cart";
import Wishlist from "./subComponent/Wishlist";
import HomeIcon from "@mui/icons-material/Home";
import AccountBoxIcon from "@mui/icons-material/AccountBox";
import ContactsIcon from "@mui/icons-material/Contacts";
import toast from "react-hot-toast";
import { useSelector } from "react-redux";

const drawerItem = [
  { name: "Home", svg: <HomeIcon /> },
  { name: "About", svg: <AccountBoxIcon /> },
  { name: "Search", svg: <SearchIcon /> },

  { name: "ContactUs", svg: <ContactsIcon /> },
];
const NavBar = ({ setMode, mode, openCart, setOpenCart }) => {
  const [open, setOpen] = useState();
  const [openDwr, setOpenDwr] = useState();
  const [openWishlist, setOpenWishlist] = useState(false);
  const { user: userData } = useSelector((state) => state.users);
  const { cartItem } = useSelector((state) => state.cart);
  const navigate = useNavigate();
  const MenuList = [
    { title: "Home" },
    { title: "About" },
    { title: "Contact" },
    { title: "Products" },
  ];
  const handleClose = () => {
    setOpen(false);
  };
  const CustonToolBar = styled(Toolbar)({
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  });
  const StyledLink = styled(Link)({
    textDecoration: "none",
    color: "white",
  });
  const handleLogout = () => {
    localStorage.clear();
    toast.success("Loggout");
    navigate("/");
    window.location.reload();
  };

  const isToken = localStorage.getItem("token");
  const avater =
    "data:image/jpeg;base64,";
  return (
    <AppBar position="sticky">
      <CustonToolBar>
        <Typography
          sx={{
            display: {
              xs: "none",
              sm: "block",
              fontSize: "25px",
              fontFamily: "cursive",
              fontWeight: 600,
              display: "flex",
              alignItems: "center",
              flexWrap: "nowrap",
              cursor: "pointer",
            },
          }}
          onClick={() => navigate("/")}
        >
          <MaleIcon
            sx={{ fontSize: "25px", fontFamily: "cursive", fontWeight: 600 }}
          />
          Mard
        </Typography>
        <IconButton
          size="large"
          edge="start"
          color="inherit"
          aria-label="menu"
          sx={{
            display: {
              xs: "flex",
              sm: "none",
              justifyContent: "center",
              alignItems: "center",
            },
          }}
          onClick={() => setOpenDwr(true)}
        >
          <MenuIcon style={{ fontWeight: 700, fontSize: "30px" }} />
        </IconButton>

        {/* <InputBase placeholder="Enter Text..." sx={{ paddingLeft: "20px" }} /> */}
        <List
          sx={{ display: { xs: "none", sm: "flex" }, alignItems: "center" }}
        >
          {MenuList.map((i, index) => (
            <ListItem>
              {" "}
              <StyledLink
                to={i.title.toLowerCase()}
                style={{ fontSize: { sm: "14px", md: "18px" } }}
                key={index}
              >
                {i.title}
              </StyledLink>
            </ListItem>
          ))}
        </List>

        <Typography
          sx={{
            display: {
              xs: "block",
              sm: "none",
              fontSize: "25px",
              fontFamily: "cursive",
              fontWeight: 600,
              display: "flex",
              alignItems: "center",
              flexWrap: "nowrap",
              cursor: "pointer",
            },
          }}
          onClick={() => navigate("/")}
        >
          <MaleIcon
            sx={{ fontSize: "25px", fontFamily: "cursive", fontWeight: 600 }}
          />
          Mard
        </Typography>

        <Box
          sx={{
            display: { xs: "none", sm: "flex" },
            alignItems: "center",
            gap: "20px",
          }}
        >
          <Switch
            color="secondary"
            onChange={() => setMode(mode === "light" ? "dark" : "light")}
          />
          <SearchIcon
            style={{ cursor: "pointer" }}
            onClick={() => navigate("/search")}
          />
          <Badge
            badgeContent={cartItem.length}
            color="error"
            onClick={() => setOpenWishlist(true)}
            style={{ cursor: "pointer" }}
          >
            <FavoriteIcon color="action" style={{ color: "white" }} />
          </Badge>
          <Wishlist
            openWishlist={openWishlist}
            setOpenWishlist={setOpenWishlist}
          />
          <Badge
            badgeContent={cartItem.length}
            color="error"
            onClick={() => setOpenCart(true)}
            sx={{ cursor: "pointer" }}
          >
            <ShoppingCartIcon color="action" style={{ color: "white" }} />
          </Badge>
          <Cart openCart={openCart} setOpenCart={setOpenCart} />

          <Avatar
            alt="Remy Sharp"
            src={userData?.profile || avater}
            onClick={() => setOpen(true)}
          />
        </Box>
        <Box
          sx={{
            display: { sx: "flex", sm: "none" },
            alignItems: "center",
            gap: "10px",
          }}
        >
          <Avatar
            alt="Remy Sharp"
            src={userData?.profile || avater}
            onClick={() => setOpen(true)}
          />
          <Menu
            sx={{ marginTop: "40px" }}
            id="basic-menu"
            open={open}
            onClose={handleClose}
            MenuListProps={{
              "aria-labelledby": "basic-button",
            }}
            anchorOrigin={{
              vertical: "left",
              horizontal: "right",
            }}
            transformOrigin={{
              vertical: "right",
              horizontal: "left",
            }}
          >
            <MenuItem onClick={handleClose}>Profile</MenuItem>
            <MenuItem onClick={handleClose}>My account</MenuItem>
            {isToken ? (
              <>
                <MenuItem
                  onClick={() => {
                    handleClose();
                    navigate("/my-orders");
                  }}
                >
                  My-Orders
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    handleClose();
                    handleLogout();
                  }}
                >
                  logout
                </MenuItem>
              </>
            ) : (
              <MenuItem
                onClick={() => {
                  handleClose();
                  navigate("/login");
                }}
              >
                Login / register
              </MenuItem>
            )}

            {userData.role === "admin" && (
              <MenuItem
                onClick={() => {
                  handleClose();
                  navigate("/admin/dashboard");
                }}
              >
                Dashboard
              </MenuItem>
            )}
          </Menu>
        </Box>
      </CustonToolBar>

      <Drawer open={openDwr} onClose={() => setOpenDwr(false)}>
        <List>
          {drawerItem.map((text, index) => (
            <ListItem
              key={index}
              disablePadding
              onClick={() => {
                setOpenDwr(false);
                navigate(`/${text.name.toLowerCase()}`);
              }}
            >
              <ListItemButton>
                <ListItemIcon>{text.svg}</ListItemIcon>
                <ListItemText primary={text.name} />
              </ListItemButton>
            </ListItem>
          ))}
          <ListItem
            disablePadding
            onClick={() => {
              setOpenCart(true);
              setOpenDwr(false);
            }}
          >
            <ListItemButton>
              <ListItemIcon>
                <Badge badgeContent={cartItem.length} color="error">
                  <ShoppingCartIcon color="action" />
                </Badge>
              </ListItemIcon>
              <ListItemText primary={"Cart"} />
            </ListItemButton>
          </ListItem>
          <ListItem
            disablePadding
            onClick={() => {
              setOpenWishlist(true);
              setOpenDwr(false);
            }}
          >
            <ListItemButton>
              <ListItemIcon>
                <Badge badgeContent={cartItem.length} color="error">
                  <FavoriteIcon color="action" />
                </Badge>
              </ListItemIcon>
              <ListItemText primary={"Wishlist"} />
            </ListItemButton>
          </ListItem>
        </List>
      </Drawer>
    </AppBar>
  );
};

export default NavBar;
