import React from "react";
import { useGetSingleOrdersQuery } from "../redux/api/orderApi";
// import { useSelector } from "react-redux";
import { useParams, useNavigate } from "react-router-dom";
import Spinner from "../components/Spinner";
import { Box, Stack, Typography } from "@mui/material";
import StepperComponent from "../components/subComponent/Stepper";
import SimilarProduct from "../components/subComponent/SimilarProduct";

const OrderDetail = () => {
  const id = useParams().id;
  const navigate = useNavigate();
  //   const { user } = useSelector((state) => state.users);
  const { data } = useGetSingleOrdersQuery(id);

  return data ? (
    <Box>
      <Typography variant="h5" textAlign={"center"} paddingY={"10px"}>
        Order Details
      </Typography>
      <Stack
        sx={{
          borderRadius: "5px",
          padding: { xs: "10px", md: "40px" },
        }}
      >
        <Typography sx={{ fontSize: "15px", color: "#808080" }}>
          Order On: 11/12/2003
        </Typography>
        <Typography sx={{ fontSize: "15px", color: "#808080" }}>
          Estimated Delivery: 11/12/2003
        </Typography>
        <Stack>
          <Typography variant="h6">Order Items</Typography>
          <hr />
          <Box
            key={data.order._id}
            sx={{
              display: "flex",

              paddingY: "10px",
              position: "relative",
              flexDirection: "row",
              flexWrap: "wrap",
              gap: "10px",
              // border: "1px solid gray",
              borderRadius: "6px",
            }}
          >
            {data?.order.orders.map((i, x) => (
              <Stack
                gap={"10px"}
                paddingX={"10px"}
                borderRadius={"4px"}
                display={"flex "}
                sx={{ cursor: "pointer" }}
                onClick={() => navigate(`/product/${i.id}`)}
              >
                <img
                  key={x}
                  src={i.thumbnail}
                  alt=""
                  style={{
                    width: "50px",
                    height: "50px",
                    borderRadius: "4px",
                    padding: "6px",
                    margin: "0 auto",
                  }}
                />{" "}
                <Stack direction={"column"} alignItems={"center"} gap={1}>
                  <Box>{i.title}</Box>
                  <Typography>QTY:{i.quantity}</Typography>

                  <Typography>Price:{i?.price}</Typography>
                  <Typography>Total:{i?.price * i.quantity}</Typography>
                </Stack>
              </Stack>
            ))}
          </Box>
          <Box>
            <Stack
              direction={"column"}
              gap={"10px"}
              paddingX={"10px"}
              flexWrap={"wrap"}
              borderRadius={"4px"}
            >
              <Typography variant="h6">Shipping Info</Typography>
              <hr />

              <Stack direction={"column"} alignItems={"start"} gap={1}>
                <Typography>Total:{data?.order.total}</Typography>
                <Typography>Address:{data?.order.shipping}</Typography>
              </Stack>
            </Stack>
          </Box>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",

              gap: "30px",
              marginTop: "20px",
            }}
          >
            <Typography variant="h6" textAlign={"center"}>
              Shipping Status
            </Typography>
            <StepperComponent
              step={
                data?.order.status === "processing"
                  ? 0
                  : data.order.status === "shipped"
                  ? 1
                  : 2
              }
            />
          </Box>
        </Stack>
        <SimilarProduct />
      </Stack>
    </Box>
  ) : (
    <Spinner />
  );
};

export default OrderDetail;
