import { Box, Divider, Typography } from "@mui/material";
import React, { useRef } from "react";
import ProductCard from "./ProductCard";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import KeyboardArrowLeftIcon from "@mui/icons-material/KeyboardArrowLeft";
import { useGetProductQuery } from "../../redux/api/productApi";
const SimilarProduct = () => {
  const similarRef = useRef();
  const { data } = useGetProductQuery();
  const handleCrousal = (side) => {
    const referance = similarRef.current;
    const ScrollAmount =
      side === "left"
        ? referance.scrollLeft - (referance.offsetWidth + 20)
        : referance.scrollLeft + (referance.offsetWidth + 20);
    referance.scrollTo({
      left: ScrollAmount,
      behavior: "smooth",
    });
  };
  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        gap: "10px",
        margin: "30px 0",

        position: "relative",
      }}
    >
      <Typography
        sx={{
          fontSize: { xs: "20px", sm: "22px", md: "28px" },
          fontWeight: 600,
          paddingX: "10px",
        }}
        variant="h4"
      >
        Similar Products
      </Typography>
      <Divider />
      <Box
        ref={similarRef}
        sx={{
          display: "flex",
          alignItems: "center",
          gap: "10px",
          overflowX: "scroll",
          "&::-webkit-scrollbar": {
            display: "none",
          },
        }}
      >
        {data?.data?.length > 0 && <ProductCard data={data?.data} />}
      </Box>

      <KeyboardArrowLeftIcon
        sx={{
          display: { xs: "none", sm: "flex" },
          position: "absolute",
          top: "50%",
          left: "20px",
          color: "black",
          fontSize: "45px",
          fontWeight: 700,
          cursor: "pointer",
          border: "1px solid gray",
          borderRadius: "50%",
          backgroundColor: "white",
        }}
        variant="contained"
        onClick={() => handleCrousal("left")}
      />

      <KeyboardArrowRightIcon
        onClick={() => handleCrousal("right")}
        sx={{
          position: "absolute",

          display: { xs: "none", sm: "flex" },

          top: "50%",
          right: "20px",
          color: "blsck",
          fontSize: "45px",
          fontWeight: 700,
          cursor: "pointer",
          border: "1px solid gray",
          borderRadius: "50%",
          backgroundColor: "white",
        }}
      />
    </Box>
  );
};

export default SimilarProduct;
