import {
  Container,
  Stack,
  TextField,
  Typography,
  Box,
  Button,
} from "@mui/material";
import { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useLoginMutation } from "../redux/api/userApi";
import toast from "react-hot-toast";
import { useDispatch } from "react-redux";
import { userExist } from "../redux/slice/userReducer";
const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();
  const [loginUser] = useLoginMutation();
  const dispatch = useDispatch();
  const handleLogin = async (e) => {
    e.preventDefault();
    if (!email || !password) return;
    const res = await loginUser({ email, password });
    if ("data" in res) {
      toast.success(res.data.message);
      localStorage.setItem("token", res.data.data.token);
      dispatch(userExist({ name: "Hemant", class: 8 }));
      navigate("/");
      window.location.reload();
    } else {
      toast.error(res.error.data.message);
    }
  };
  const style = {
    p: 4,
    display: "flex",
    flexDirection: "column",
    gap: "4px",
  };
  return (
    <Container
      sx={{
        display: "flex",
        minHeight: "100vh",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
      }}
    >
      <Typography fontSize={"20px"}>Login</Typography>
      <Box
        sx={{
          display: "flex",

          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <form style={style} onSubmit={handleLogin}>
          <TextField
            required
            id="standard-required"
            label="Email"
            variant="standard"
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <TextField
            required
            id="filled-password-input"
            label="Password"
            type="password"
            autoComplete="current-password"
            variant="standard"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />

          <Box sx={{ textAlign: "right", marginTop: "10px" }}>
            <Button type="submit" variant="contained" fullWidth>
              Submit
            </Button>
          </Box>
        </form>

        <Stack direction={"row"} gap={"10px"} marginTop={2}>
          {" "}
          <Typography>Not Registered?</Typography>
          <Link to={"/register"} style={{ textDecoration: "none" }}>
            Register
          </Link>
        </Stack>
      </Box>
    </Container>
  );
};

export default Login;
