import { Avatar, Box, Typography } from "@mui/material";
import React from "react";
import ReactStars from "react-stars";
const Reviwe = () => {
  return (
    <Box
      sx={{
        display: "flex",
        gap: "10px",
        maxWidth: {
          xs: "full",
          sm: "270px",
          md: "400px",
          padding: "6px",
          border: "1px solid gray",
          borderRadius: "5px",
        },
      }}
    >
      <Avatar src="data:image/jpeg;base64," />
      <Box>
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          {" "}
          <Typography variant="h7">Hemanta Kumar Paswan</Typography>
          <Typography sx={{ fontSize: "15px" }}>01/04/2024</Typography>
        </Box>
        <ReactStars count={5} value={5} size={24} color2={"#ffd700"} />
        <Typography variant="p">
          Lorem ipsum dolor sit amet consectetur, adipisicing elit. Quis sed
          quae cum consequuntur ab dolore, neque commodi totam nesciunt ea
          exercitationem expedita laborum voluptatibus sit, fuga architecto
          facilis esse fugiat!
        </Typography>
      </Box>
    </Box>
  );
};

export default Reviwe;
