import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

const userApi = createApi({
  reducerPath: "userApi",
  baseQuery: fetchBaseQuery({ baseUrl: process.env.REACT_APP_BEURL }),
  endpoints: (builder) => ({
    getUser: builder.query({
      query: (id) => `/api/user/${id}`,
    }),
    createUser: builder.mutation({
      query: (data) => ({
        url: `/api/user/new`,
        method: "POST",
        body: data,
      }),
    }),
    login: builder.mutation({
      query: (data) => ({
        method: "POST",
        url: "/api/user/login",
        body: data,
      }),
    }),
  }),
});

export const { useGetUserQuery, useCreateUserMutation, useLoginMutation } =
  userApi;

export default userApi;
