import {
  Box,
  Button,
  Container,
  Stack,
  TextField,
  Typography,
  styled,
} from "@mui/material";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useCreateUserMutation } from "../redux/api/userApi";
import toast from "react-hot-toast";
import { useNavigate } from "react-router-dom";
const avater = "https://cdn-icons-png.flaticon.com/512/147/147142.png";
const style = {
  p: 4,
  display: "flex",
  flexDirection: "column",
  gap: "4px",
  marginTop: "10px",
};
const Register = () => {
  const InputImage = styled("input")({});
  const [img, setImg] = useState("");
  const [profile, setProfile] = useState("");
  const [password, setPassword] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [address, setAddress] = useState("");
  const [landMark, setLandMark] = useState("");
  const [city, setCity] = useState("");
  const [state, setState] = useState("");
  const [pin, setPin] = useState();
  const [number, setNumber] = useState();
  const [registerUser] = useCreateUserMutation();
  const navigate = useNavigate();

  const handleUplode = (event) => {
    const selectedFile = event.target.files[0];
    setProfile(event.target.files[0]);
    const reader = new FileReader();
    reader.onload = (e) => {
      setImg(e.target.result); // Update image state with data URL
    };

    reader.readAsDataURL(selectedFile);
  };
  const handleRegister = async (e) => {
    e.preventDefault();
    if (
      (!name,
      !email,
      !number,
      !password,
      !address,
      !landMark,
      !city,
      !state,
      !pin)
    )
      return toast.error("Enter All Fields");
    const formData = new FormData();
    formData.set("name", name);
    formData.set("email", email);
    formData.set("number", number);
    formData.set("password", password);
    formData.set("address", address);
    formData.set("landMark", landMark);
    formData.set("city", city);
    formData.set("state", state);
    formData.set("pin", pin);
    formData.set("img", profile);

    const res = await registerUser(formData);
    if ("data" in res) {
      toast.success(res.data.message);
      setName("");
      setEmail("");
      setNumber("");
      setPassword("");
      setAddress("");
      setLandMark("");
      setCity("");
      setState("");
      setPin("");
      setImg("");
      navigate("/login");
    }
    if ("error" in res) {
      toast.error(res.error.data.message);
    }
  };
  return (
    <Container
      sx={{
        display: "flex",
        minHeight: "100vh",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
      }}
    >
      <Typography fontSize={"20px"}>New Registration</Typography>
      <Box
        sx={{
          display: "flex",

          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <form style={style} onSubmit={handleRegister}>
          <label
            htmlFor="profile"
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              width: "80px",
              height: "80px",
              margin: "0 auto",
            }}
          >
            <img
              src={img || avater}
              width={"80px"}
              height={"80px"}
              alt=""
              style={{ borderRadius: "50%" }}
            />
          </label>
          <InputImage
            id="profile"
            type="file"
            htmlFor="profile"
            sx={{ display: "none" }}
            onChange={handleUplode}
          />
          <TextField
            required
            id="standard-required"
            label="Full Name"
            variant="standard"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          <TextField
            required
            id="standard-required"
            label="Email"
            variant="standard"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <TextField
            required
            id="standard-read-only-input"
            label="Phone Number"
            variant="standard"
            type="number"
            onChange={(e) => setNumber(e.target.value)}
            value={number}
          />
          <TextField
            required
            id="filled-password-input"
            label="Password"
            type="password"
            autoComplete="current-password"
            variant="standard"
            onChange={(e) => setPassword(e.target.value)}
            value={password}
          />
          <TextField
            required
            id="standard-required"
            label="Address"
            variant="standard"
            value={address}
            onChange={(e) => setAddress(e.target.value)}
          />
          <TextField
            id="standard-search"
            label="Land Mark"
            variant="standard"
            onChange={(e) => setLandMark(e.target.value)}
            value={landMark}
          />
          <TextField
            required
            id="standard-disabled"
            label="City"
            variant="standard"
            onChange={(e) => setCity(e.target.value)}
            value={city}
          />
          <TextField
            required
            id="standard-password-input"
            label="State"
            variant="standard"
            onChange={(e) => setState(e.target.value)}
            value={state}
          />

          <TextField
            required
            id="standard-number"
            label="Pincode"
            type="number"
            variant="standard"
            onChange={(e) => setPin(e.target.value)}
            value={pin}
          />

          <Box sx={{ textAlign: "right", marginTop: "10px" }}>
            <Button type="submit" variant="contained" fullWidth>
              Submit
            </Button>
          </Box>
        </form>

        <Stack direction={"row"} gap={"10px"}>
          {" "}
          <Typography>Already Registered?</Typography>
          <Link to={"/login"} style={{ textDecoration: "none" }}>
            Login
          </Link>
        </Stack>
      </Box>
    </Container>
  );
};

export default Register;
